.light{
  background: #eee;
  color: rgb(0 0 0);
}

.dark {
  background: #1E202A;
  color: rgb(241 245 249);
}

.switch {
  position: relative;
  display: inline-block;
  width: 40px;
  height: 24px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #ccc;
  transition: .4s;
}

input:checked+.slider{
  background: linear-gradient(#378FE6, #3EDA82 60%);
}

.slider:before {
  position: absolute;
  content: "";
  height: 16px;
  width: 16px;
  left: 5px;
  bottom: 4px;
  background-color: white;
  transition: .6s ease-in-out;
}

input:checked+.slider:before {
  background-color: hsl(230, 17%, 14%);
}

input:focus+.slider {
  outline: none;
  border: none;
}

input:checked+.slider:before {
  -webkit-transform: translateX(15px);
  transform: translateX(15px);
}

/* Rounded sliders */

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

.tracking-widest {
  letter-spacing: 0.3rem;
  color: #a2a8c0;
  margin-bottom: 1rem;
}

.card-instagram{
  background: linear-gradient(to right, #fdc468, #df4996);
